import prevod from "./prevod";
import useLanguage from "../../hooks/utils/useLanguage";
import "../../styles/textSection.css";
import { Typography, Grid } from "@mui/material";

const TextSection = () => {
  const lang = useLanguage(prevod);
  return (
    <div className="background-image-text" id="textSection">
      <div className="text-container-text">
        <Grid container direction="column" alignContent="center">
          <Grid item>
            <Typography className="title-short-text">
              {lang.textSection}
            </Typography>
          </Grid>
          <Grid item>
            <Typography className="title-short-text">
              {lang.textSection2}
            </Typography>
          </Grid>
          <Grid item>
            <Typography className="subtitle-short-text">
              {lang.slogan}
            </Typography>
          </Grid>
        </Grid>
      </div>
    </div>
  );
};

export default TextSection;
