import useLanguage from "../../hooks/utils/useLanguage";
import prevod from "./prevod";
import TextUnderline from "../TextUnderline";
import "../../styles/contact.css";
import React from "react";

import { Button, Grid, IconButton, Typography } from "@mui/material";
import MailOutlineIcon from "@mui/icons-material/MailOutline";
import { PopupModal } from "react-calendly";

const Contact = () => {
  const lang = useLanguage(prevod);
  const root = document.getElementById("root");
  const rootElement = root ? root : undefined;
  const [open, setOpen] = React.useState(false);
  return (
    <div className="background-image-contact" id="contact">
      <div
        className="text-container-contact"
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <TextUnderline color="#FEFEFE" text={lang.contact} />
        <div className="grid-container">
          <Grid
            container
            direction="row"
            justifyContent="center"
            alignItems="center"
            sx={{
              flexDirection: {
                xs: "column",
                sm: "column",
                md: "row",
                lg: "row",
                xl: "row",
              },
            }}
            spacing={3}
          >
            <Grid item>
              <img
                src="./contactUs.gif"
                alt="Contacting"
                className="image-contact"
              />
            </Grid>
            <Grid item>
              <Grid
                container
                direction="column"
                alignItems="center"
                spacing={3}
              >
                <Grid item>
                  <Grid
                    container
                    direction="row"
                    spacing={3}
                    justifyContent="flex-start"
                    alignItems="center"
                  >
                    <Grid item>
                      <IconButton>
                        <MailOutlineIcon className="icon-contact" />
                      </IconButton>
                    </Grid>
                    <Grid item>
                      <Typography className="text-contact">
                        3nityoffice@gmail.com
                      </Typography>
                    </Grid>
                  </Grid>
                </Grid>
                <Grid item>
                  <Button
                    variant="outlined"
                    className="button-contact"
                    onClick={() => {
                      setOpen(true);
                    }}
                  >
                    {lang.appoint}
                  </Button>
                  {rootElement ? (
                    <PopupModal
                      url="https://calendly.com/3nityoffice"
                      onModalClose={() => setOpen(false)}
                      open={open}
                      rootElement={rootElement}
                      pageSettings={{
                        backgroundColor: "#262626",
                        primaryColor: "#41D2BF",
                        textColor: "#FEFEFE",
                      }}
                    />
                  ) : null}
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </div>
      </div>
    </div>
  );
};

export default Contact;
