import prevod from "./prevod";
import useLanguage from "../../hooks/utils/useLanguage";
import TextUnderline from "../TextUnderline";
import "../../styles/services.css";
import { Grid, Typography } from "@mui/material";

const ServiceItem = ({
  image,
  title,
  text,
}: {
  image: string;
  title: string;
  text: string;
}) => {
  return (
    <Grid
      container
      direction="column"
      alignItems="center"
      justifyContent="flex-start"
      className="service-item"
    >
      <Grid item>
        <img src={image} alt="Building A Website" className="picture-icon" />
      </Grid>
      <Grid item>
        <Typography className="service-title">{title}</Typography>
      </Grid>
      <Grid item>
        <Typography className="service-text">{text}</Typography>
      </Grid>
    </Grid>
  );
};

const Services = () => {
  const lang = useLanguage(prevod);
  return (
    <div className="background-image-services" id="services">
      <Grid
        container
        className="text-container-services"
        direction="column"
        alignItems="center"
      >
        <Grid item>
          <TextUnderline text={lang.title} color="#FEFEFE" />
        </Grid>
        <Grid item>
          <Grid
            container
            justifyContent="center"
            alignItems="center"
            sx={{ flexDirection: { xs: "column", sm: "column", md: "column", lg: "row" , xl: "row"  } }}
            spacing={12}
          >
            <Grid item>
              <ServiceItem
                image="./buildingWebsite.png"
                title={lang.building}
                text={lang.buildingText}
              />
            </Grid>
            <Grid item>
              <ServiceItem
                image="./mobile.png"
                title={lang.mobile}
                text={lang.mobileText}
              />
            </Grid>
            <Grid item>
              <ServiceItem
                image="./workflow.png"
                title={lang.workflow}
                text={lang.workflowText}
              />
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </div>
  );
};

export default Services;
